import axios from 'axios'
import {ToastProgrammatic as Toast,ModalProgrammatic as Modal,NotificationProgrammatic as Notification} from 'buefy' // buefy UI
import router from '../router'

axios.defaults.headers['Content-Type'] = 'application/json;charset=utf-8'

const service = axios.create({
    timeout: 10000, // 7
    baseURL: process.env.VUE_APP_BASE_API,
    //publicPath: process.env.VUE_APP_BASE_API,
})

// 
service.interceptors.request.use(
    config => {
        if(localStorage.token) {
            config.headers.token = localStorage.token;
        }
        return config;
    },
    error => {
        console.log(error);
        return Promise.reject(error);
    }
)

// 
service.interceptors.response.use(
    response => {
        // 
        const res = response.data;
        const status=response.status;//
        if(status == 200){
            if(res.code == 20000 ||res.code== 10000 || res.code==200 || res.data || res.ErrorCode == 20000) {
                return res;
            }else {
                let message = (res.error && res.error.message) || res.message || res.msg || '';
                Notification.open({
                    message: message,
                    duration: 5000,
                    progressBar: true,
                    pauseOnHover: true,
                    type: 'is-danger',
                    hasIcon: true
                });
                console.log(':', response.data);
                // 500,
                // if(res.ErrorCode == 500) {
                //     router.push('/login')
                // }
                return Promise.reject(
                    new Error(res.message || (res.error &&res.error.message) || '')
                );
            }
        }else{
            let message = (response.error && response.error.message) || response.message || res.msg || '';
            Notification.open({
                message: message,
                duration: 5000,
                progressBar: true,
                pauseOnHover: true,
                type: 'is-danger',
                hasIcon: true
            });
            console.log(':', response);
            return Promise.reject(
                new Error(response.msg || (response.error &&response.error.message) || '')
            );
        }
    },
    error => {
        console.log(':', error);
        if(error.response) {
            // error.response,,
            // ,,reject()
            let code = error.response.status;
            if(code == 401) {
                // router.push('/login');
                return Promise.reject(
                    new Error(',')
                );
            }
            Toast.open({
                message: ', !  ' + code,
                type: 'is-warning',
                duration: 4 * 1000,
                position: 'is-top',
                pauseOnHover: true
            });
            return Promise.reject(error.message);
        }else {
            // 
            // reject
            return Promise.reject(error.message);
        }
    }
)
export default service;